import React from 'react';
import { X, GripVertical } from 'lucide-react';

const FormField = ({ 
  field, 
  index, 
  onUpdate, 
  onDelete, 
  isPreview = false,
  value = '',
  onChange = () => {}
}) => {
  const handleFieldUpdate = (updates) => {
    onUpdate(index, { ...field, ...updates });
  };

  const renderField = () => {
    switch (field.type) {
      case 'text':
        return (
          <input
            type="text"
            placeholder={field.placeholder || 'Enter text...'}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            required={field.required}
            disabled={isPreview}
          />
        );

      case 'email':
        return (
          <input
            type="email"
            placeholder={field.placeholder || 'Enter email...'}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            required={field.required}
            disabled={isPreview}
          />
        );

      case 'number':
        return (
          <input
            type="number"
            placeholder={field.placeholder || 'Enter number...'}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            required={field.required}
            disabled={isPreview}
          />
        );

      case 'textarea':
        return (
          <textarea
            placeholder={field.placeholder || 'Enter text...'}
            rows={field.rows || 3}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent resize-vertical"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            required={field.required}
            disabled={isPreview}
          />
        );

      case 'select':
        return (
          <select
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            required={field.required}
            disabled={isPreview}
          >
            <option value="">Select an option...</option>
            {field.options?.map((option, idx) => (
              <option key={idx} value={option}>
                {option}
              </option>
            ))}
          </select>
        );

      case 'radio':
        return (
          <div className="space-y-2">
            {field.options?.map((option, idx) => (
              <label key={idx} className="flex items-center space-x-2">
                <input
                  type="radio"
                  name={`field-${index}`}
                  value={option}
                  checked={value === option}
                  onChange={(e) => onChange(e.target.value)}
                  required={field.required}
                  disabled={isPreview}
                  className="text-primary-600 focus:ring-primary-500"
                />
                <span className="text-sm text-gray-700">{option}</span>
              </label>
            ))}
          </div>
        );

      case 'checkbox':
        return (
          <div className="space-y-2">
            {field.options?.map((option, idx) => (
              <label key={idx} className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  value={option}
                  checked={Array.isArray(value) && value.includes(option)}
                  onChange={(e) => {
                    const newValue = Array.isArray(value) ? [...value] : [];
                    if (e.target.checked) {
                      newValue.push(option);
                    } else {
                      const index = newValue.indexOf(option);
                      if (index > -1) {
                        newValue.splice(index, 1);
                      }
                    }
                    onChange(newValue);
                  }}
                  disabled={isPreview}
                  className="text-primary-600 focus:ring-primary-500"
                />
                <span className="text-sm text-gray-700">{option}</span>
              </label>
            ))}
          </div>
        );

      case 'date':
        return (
          <input
            type="date"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            value={value}
            onChange={(e) => onChange(e.target.value)}
            required={field.required}
            disabled={isPreview}
          />
        );

      default:
        return <div className="text-red-500">Unknown field type: {field.type}</div>;
    }
  };

  if (isPreview) {
    return (
      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-2">
          {field.label}
          {field.required && <span className="text-red-500 ml-1">*</span>}
        </label>
        {renderField()}
        {field.description && (
          <p className="mt-1 text-sm text-gray-500">{field.description}</p>
        )}
      </div>
    );
  }

  return (
    <div className="bg-white border border-gray-200 rounded-lg p-4 mb-4 shadow-sm hover:shadow-md transition-shadow">
      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center space-x-2">
          <GripVertical className="h-5 w-5 text-gray-400 cursor-move" />
          <span className="text-sm font-medium text-gray-700">
            {field.type.charAt(0).toUpperCase() + field.type.slice(1)} Field
          </span>
        </div>
        <button
          onClick={() => onDelete(index)}
          className="text-red-500 hover:text-red-700 transition-colors"
        >
          <X className="h-4 w-4" />
        </button>
      </div>

      <div className="space-y-3">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Label
          </label>
          <input
            type="text"
            value={field.label || ''}
            onChange={(e) => handleFieldUpdate({ label: e.target.value })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm"
            placeholder="Field label"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Description (optional)
          </label>
          <input
            type="text"
            value={field.description || ''}
            onChange={(e) => handleFieldUpdate({ description: e.target.value })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm"
            placeholder="Field description"
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Placeholder (optional)
          </label>
          <input
            type="text"
            value={field.placeholder || ''}
            onChange={(e) => handleFieldUpdate({ placeholder: e.target.value })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm"
            placeholder="Placeholder text"
          />
        </div>

        {(field.type === 'select' || field.type === 'radio' || field.type === 'checkbox') && (
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Options (one per line)
            </label>
            <textarea
              value={field.options?.join('\n') || ''}
              onChange={(e) => handleFieldUpdate({ 
                options: e.target.value.split('\n').filter(option => option.trim()) 
              })}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm resize-vertical"
              rows={3}
              placeholder="Option 1&#10;Option 2&#10;Option 3"
            />
          </div>
        )}

        {field.type === 'textarea' && (
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Rows
            </label>
            <input
              type="number"
              value={field.rows || 3}
              onChange={(e) => handleFieldUpdate({ rows: parseInt(e.target.value) || 3 })}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent text-sm"
              min="1"
              max="10"
            />
          </div>
        )}

        <div className="flex items-center">
          <input
            type="checkbox"
            id={`required-${index}`}
            checked={field.required || false}
            onChange={(e) => handleFieldUpdate({ required: e.target.checked })}
            className="text-primary-600 focus:ring-primary-500"
          />
          <label htmlFor={`required-${index}`} className="ml-2 text-sm text-gray-700">
            Required field
          </label>
        </div>
      </div>
    </div>
  );
};

export default FormField;
